export const softMinifyCssStrings = (cssString = " ") =>
	cssString.replace(/\s+/g, " ").replace(/\.ffff\-[\w\-\s\.\,\:\>\(\)\d\+\[\]\#\>]+\{[\s]+\}/g, "");

export const renderBlockCustomCss = (cssString = '', blockId) =>
	cssString.replace(new RegExp('{{FFBID}}', "g"), '.' + blockId)

export const RenderDynamicTag = ({ tagName, className, children }) => {
	const DynamicTag = tagName;
	return <DynamicTag className={className}>{children}</DynamicTag>
}

export const blockEditorWrapperClass = (useBlockProps, attributes, defaultClass = '') => {
	const { blockId, mainWrapShowHide } = attributes;
	const editorWrapClass = useBlockProps({
		className: `${blockId} ffblock-block-editor${mainWrapShowHide?.lg ? ' ffblock-hide-desktop' : ''}${mainWrapShowHide?.md ? ' ffblock-hide-tablet' : ''}${mainWrapShowHide?.sm ? ' ffblock-hide-mobile' : ''} ${defaultClass ? ' ' + defaultClass : ''}`
	})
	return editorWrapClass;
}

export const blockFrontendWrapperClass = (useBlockProps, attributes) => {
	const { blockId, mainWrapShowHide } = attributes;
	const editorWrapClass = useBlockProps.save({
		className: `${blockId} ffblock-block-frontend${mainWrapShowHide?.lg ? ' ffblock-hide-desktop' : ''}${mainWrapShowHide?.md ? ' ffblock-hide-tablet' : ''}${mainWrapShowHide?.sm ? ' ffblock-hide-mobile' : ''}`
	})
	return editorWrapClass;
}

export const printHtmlStringMarkup = (htmlString = '') => {
	return { __html: htmlString };
};