import { _device, _push } from "./styleHelperFunc";

function backgroundStyle({ controlValue }) {
	let deskBackground = '';
	let tabBackground = '';
	let mobBackground = '';

	if (controlValue !== undefined) {
		let data = { lg: [], md: [], sm: [] };
		let bgWithoutResposiveData = '';

		if (controlValue.type === 'classic') {
			if (controlValue.classic.imgProperty['imgPosition']) {
				data = _push(_device(controlValue.classic.imgProperty['imgPosition'], 'background-position:{{key}}'), data);
			}
			if (controlValue.classic.imgProperty['imgAttachment']) {
				data = _push(_device(controlValue.classic.imgProperty['imgAttachment'], 'background-attachment:{{key}}'), data);
			}
			if (controlValue.classic.imgProperty['imgRepeat']) {
				data = _push(_device(controlValue.classic.imgProperty['imgRepeat'], 'background-repeat:{{key}}'), data);
			}
			if (controlValue.classic.imgProperty['imgSize']) {
				data = _push(_device(controlValue.classic.imgProperty['imgSize'], 'background-size:{{key}}'), data);
			}

			let imgURLVal = controlValue['classic']['img'] !== undefined ? controlValue['classic']['img']['imgURL'] : '';

			bgWithoutResposiveData += controlValue.classic['color'] ? 'background:' + controlValue.classic['color'] + ';' : '';
			bgWithoutResposiveData += (imgURLVal ? 'background-image:url(' + imgURLVal + ');' : '');

		} else if (controlValue.type === 'gradient' && controlValue.gradient) {
			bgWithoutResposiveData += controlValue.gradient ? 'background-image :' + controlValue.gradient + ';' : '';
		}
		deskBackground = `${bgWithoutResposiveData} ${data.lg.length > 0 ? data.lg.join(';') + ';' : ''}`;
		tabBackground = `${bgWithoutResposiveData} ${data.md.length > 0 ? data.md.join(';') + ';' : ''}`;
		mobBackground = `${bgWithoutResposiveData} ${data.sm.length > 0 ? data.sm.join(';') + ';' : ''}`;
	}

	return {
		deskBackground,
		tabBackground,
		mobBackground
	}
}

export default backgroundStyle;