import { __ } from '@wordpress/i18n';
import { Tooltip } from '@wordpress/components';
import "./scss/alignment.scss";
import Devices from "./Devices";

const IconControl = (props) => {

	const { value, responsive, onChange, label, options, resRequiredProps } = props;
	const { resDevice } = resRequiredProps;

	const _filterValue = () => {
		return value ? (responsive ? (value[resDevice]) : value) : '';
	}

	const setSettings = (val) => {
		if (val == '') {
			return;
		}
		const final = responsive ? Object.assign({}, value, { [resDevice]: val }) : val;
		onChange(final);
	}

	const defaultData = options && Array.isArray(options) ? options : [];

	if (defaultData.length !== 0) {
		return (
			<div className="ffblock-control-field ffblock-control-alignment-wrap">

				{(label || responsive) && (
					<div className="ffblock-control-head">

						{label && (
							<span className="ffblock-label">{label}</span>
						)}

						{responsive &&
							<Devices
								resRequiredProps={resRequiredProps}
							/>
						}
					</div>
				)}

				<div className="ffblock-control-body ffblock-btn-group">
					{defaultData.map((data, index) => {
						return (
							<button className={(_filterValue() == data.value ? 'active' : '') + ' ffblock-button'} key={index} onClick={() => setSettings(_filterValue() == data.value ? '' : data.value)}>
								<Tooltip text={__(data.label, 'fluentform-block')}><span className="ffblock-align-icons">{__(data.icon, 'fluentform-block')}</span></Tooltip>
							</button>
						)
					})}
				</div>

			</div>
		)
	} else {
		return (
			<h5>{__('Component props options is mandatory', 'fluentform-block')}</h5>
		)
	}

}

export default IconControl;