import { __ } from '@wordpress/i18n';
import { MediaUpload } from '@wordpress/blockEditor';
import { BaseControl, Button } from '@wordpress/components';
import Color from "../Color";
import ImageAvater from "./ImageAvater";
import SelectBGDevice from "./SelectBGDevice";
import '../scss/bgImage.scss'

import {
	BACKGROUND_POSITION, BACKGROUND_ATTACHMENT, BACKGROUND_REPEAT, BACKGROUND_SIZE
} from '../../globals/constants';


function BGImage({ label, value, onChange, resRequiredProps }) {


	const responsive = true;
	const resAttr = ['imgPosition', 'imgAttachment', 'imgRepeat', 'imgSize'];

	const setSettings = (val, type) => {
		const newData = JSON.parse(JSON.stringify(value));

		if (resAttr.includes(type)) {
			if (responsive) {
				newData['imgProperty'] = val;
			} else {
				newData['imgProperty'] = val;
			}
		} else {
			newData[type] = val;
		}

		onChange(newData)

	}

	const imgURLVal = value['img'] !== undefined ? value['img']['imgURL'] : '';
	const imgIDVal = value['img'] !== undefined ? value['img']['imgID'] : '';

	return (
		<div className="ffblock-control-field ffblock-control-bg-img-wrap">

			{label && (
				<span className="ffblock-label">{label}</span>
			)}

			<div className="ffblock-bg-img">
				<Color
					label={__('Background Color', 'fluentform-block')}
					color={value['color'] || ''}
					onChange={(val) => setSettings(val, 'color')}
				/>

				<BaseControl label={__("Background Image", 'fluentform-block')}>
					<MediaUpload
						onSelect={(media) => {
							const imgobj = {
								imgURL: media.url,
								imgID: media.id
							}
							setSettings(imgobj, 'img')
						}
						}
						allowedTypes={["image"]}
						value={imgIDVal}
						render={({ open }) => {
							if (!imgURLVal) {
								return (
									<Button
										className="ffblock-img-remove-btn"
										label={__("Upload Image", "fluentform-block")}
										icon="format-image"
										onClick={open}
									/>
								);
							} else {
								return (
									<ImageAvater
										imageUrl={imgURLVal}
										onEditImage={open}
										onDeleteImage={() => {
											setSettings('', 'img');
										}}
									/>
								)
							}
						}}
					/>

					{imgURLVal && (
						<SelectBGDevice
							label={__('Background Position', 'fluentform-block')}
							responsive={true}
							value={value['imgProperty']}
							name={'imgPosition'}
							options={BACKGROUND_POSITION}
							onChange={(val) => {
								setSettings(val, 'imgPosition')
							}}
							resRequiredProps={resRequiredProps}
						/>

					)}

					{imgURLVal && (
						<SelectBGDevice
							label={__('Background Attachment', 'fluentform-block')}
							responsive={true}
							value={value['imgProperty']}
							name={'imgAttachment'}
							options={BACKGROUND_ATTACHMENT}
							onChange={(val) => setSettings(val, 'imgAttachment')}
							resRequiredProps={resRequiredProps}
						/>
					)}

					{imgURLVal && (
						<SelectBGDevice
							label={__('Background Repeat', 'fluentform-block')}
							responsive={true}
							value={value['imgProperty']}
							name={'imgRepeat'}
							options={BACKGROUND_REPEAT}
							onChange={(val) => setSettings(val, 'imgRepeat')}
							resRequiredProps={resRequiredProps}
						/>
					)}

					{imgURLVal && (
						<SelectBGDevice
							label={__('Background Size', 'fluentform-block')}
							responsive={true}
							value={value['imgProperty']}
							name={'imgSize'}
							options={BACKGROUND_SIZE}
							onChange={(val) => setSettings(val, 'imgSize')}
							resRequiredProps={resRequiredProps}
						/>
					)}
				</BaseControl>

			</div>

		</div>
	)
}

export default BGImage;