function boxshadowStyle({ controlValue }) {
	let boxshadowStyles = '';
	if (controlValue !== undefined) {
		boxshadowStyles = controlValue.color ? 'box-shadow:' + (controlValue.inset ? 'inset' : '') + ' ' + controlValue.width.top + 'px ' + controlValue.width.right + 'px ' + controlValue.width.bottom + 'px ' + controlValue.width.left + 'px ' + controlValue.color + ';' : '';
	}
	return {
		boxshadowStyles
	}
}

export default boxshadowStyle;