import { _device, _push } from './styleHelperFunc';

function typographyStyle({ controlValue }) {
	let deskTypography = '';
	let tabTypography = '';
	let mobTypography = '';

	if (controlValue !== undefined) {
		let font = '';
		// if (controlValue.family && controlValue.family !== 'none') {
		// 	if (!['Arial', 'Tahoma', 'Verdana', 'Helvetica', 'Times New Roman', 'Trebuchet MS', 'Georgia'].includes(controlValue.family)) {
		// 		font = "@import url(https://fonts.googleapis.com/css?family=" + controlValue.family.replace(' ', '+') + ':' + (controlValue.weight || 400) + ");";
		// 	}
		// }
		let data = { lg: [], md: [], sm: [] };
		if (controlValue.size) {
			data = _push(_device(controlValue.size, 'font-size:{{key}}'), data);
		}
		if (controlValue.height) {
			data = _push(_device(controlValue.height, 'line-height:{{key}}'), data);
		}
		if (controlValue.spacing) {
			data = _push(_device(controlValue.spacing, 'letter-spacing:{{key}}'), data);
		}

		const withoutResData = (controlValue.family && controlValue.family !== 'none' ? "font-family:" + controlValue.family + "," + (controlValue.type || "sans-serif") + ";" : '') + (controlValue.weight ? 'font-weight:' + controlValue.weight + ';' : '') + (controlValue.style ? 'font-style:' + controlValue.style + ';' : '') + (controlValue.transform ? 'text-transform:' + controlValue.transform + ';' : '') + (controlValue.decoration ? 'text-decoration:' + controlValue.decoration + ';' : '');

		deskTypography = `${withoutResData}${data.lg.length > 0 ? data.lg.join(';') + ';' : ''}`;
		tabTypography = `${data.lg.length > 0 ? data.lg.join(';') + ';' : ''}`;
		mobTypography = `${data.lg.length > 0 ? data.lg.join(';') + ';' : ''}`;

		// console.log({
		// 	deskTypography,
		// 	tabTypography,
		// 	mobTypography
		// });
	}

	return {
		deskTypography,
		tabTypography,
		mobTypography,
	}
}

export default typographyStyle;