import { __ } from '@wordpress/i18n';
import {
	Button,
	ButtonGroup,
	PanelBody,
	ToggleControl
} from "@wordpress/components";

import {
	NORMAL_HOVER
} from "../globals/constants";

import {
	Dimension,
	RangeDevice,
	Background,
	BorderDimension,
	BoxShadow,
	Range,
	TabPanelControl,
	CustomCss
} from '../components';

function AdvancedControl({ attributes, setAttributes, type = '' }) {
	const {
		resDevice,
		mainWrapMarginTop,
		mainWrapMarginBottom,
		mainWrapMargin,
		mainWrapPadding,
		mainWrapBGType,
		mainWrapBG,
		mainWrapHoverBG,
		mainWrapHoverBGTransition,
		mainWrapBGOverlayEnable,
		mainWrapBGOverlay,
		mainWrapBorderType,
		mainWrapBorder,
		mainWrapHoverBorder,
		mainWrapRadius,
		mainWrapHoverRadius,
		mainWrapShadowType,
		mainWrapShadow,
		mainWrapHoverShadow,
		mainWrapZindex,
		mainWrapShowHide
	} = attributes

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	return (
		<>

			<PanelBody title={__('Margin & Padding', 'fluentform-block')} initialOpen={true}>

				{type === 'row' && (
					<>
						<RangeDevice
							label={__('Margin Top', 'fluentform-block')}
							responsive={true}
							value={mainWrapMarginTop}
							min={-500}
							max={500}
							step={1}
							onChange={(val) => setAttributes({ mainWrapMarginTop: val })}
							resRequiredProps={resRequiredProps}
						/>

						<RangeDevice
							label={__('Margin Bottom', 'fluentform-block')}
							responsive={true}
							value={mainWrapMarginBottom}
							min={-500}
							max={500}
							step={1}
							onChange={(val) => setAttributes({ mainWrapMarginBottom: val })}
							resRequiredProps={resRequiredProps}
						/>
					</>
				)}
				{type !== 'row' && (
					<Dimension
						onChange={(value) => {
							setAttributes({ mainWrapMargin: value })
						}}
						label="Margin"
						type="margin"
						responsive={true}
						value={mainWrapMargin}
						resRequiredProps={resRequiredProps}
					/>
				)}
				<Dimension
					label={__('Padding', 'fluentform-block')}
					type="padding"
					responsive={true}
					value={mainWrapPadding}
					onChange={(value) => {
						setAttributes({ mainWrapPadding: value })
					}}
					resRequiredProps={resRequiredProps}
				/>

			</PanelBody>

			<PanelBody title={__('Background', 'fluentform-block')} initialOpen={false}>

				<TabPanelControl
					normalContent={
						<Background
							label={"Background"}
							value={mainWrapBG}
							onChange={val => setAttributes({ mainWrapBG: val })}
							resRequiredProps={resRequiredProps}
						/>
					}
					hoverContent={
						<>
							<Background
								label={"Background"}
								value={mainWrapHoverBG}
								onChange={val => setAttributes({ mainWrapHoverBG: val })}
								resRequiredProps={resRequiredProps}
							/>
							<Range
								label={__("Background Transition", 'fluentform-block')}
								reset={true}
								value={mainWrapHoverBGTransition}
								onChange={(val) => setAttributes({ mainWrapHoverBGTransition: val })}
								min={0}
								max={5}
								step={0.1}
							/>
						</>
					}
				/>

				<ToggleControl
					label={__("Enable Overlay", "fluentform-block")}
					checked={mainWrapBGOverlayEnable}
					onChange={(mainWrapBGOverlayEnable) =>
						setAttributes({ mainWrapBGOverlayEnable })
					}
				/>

				{mainWrapBGOverlayEnable && (
					<>
						<Background
							label={"Background"}
							value={mainWrapBGOverlay}
							onChange={val => setAttributes({ mainWrapBGOverlay: val })}
							resRequiredProps={resRequiredProps}
						/>
					</>
				)}

			</PanelBody>

			<PanelBody title={__('Border', 'fluentform-block')} initialOpen={false}>
				<TabPanelControl
					normalContent={
						<>
							<BorderDimension
								value={mainWrapBorder}
								onChange={(value) => {
									setAttributes({ mainWrapBorder: value })
								}}
								resRequiredProps={resRequiredProps}
							/>
							<Dimension
								label={__('Border Radius', 'fluentform-block')}
								type="borderRadius"
								responsive={true}
								value={mainWrapRadius}
								onChange={(value) => {
									setAttributes({ mainWrapRadius: value })
								}}
								resRequiredProps={resRequiredProps}
							/>
						</>
					}
					hoverContent={
						<>
							<BorderDimension
								value={mainWrapHoverBorder}
								onChange={(value) => {
									setAttributes({ mainWrapHoverBorder: value })
								}}
								resRequiredProps={resRequiredProps}
							/>
							<Dimension
								label={__('Border Radius', 'fluentform-block')}
								type="borderRadius"
								responsive={true}
								value={mainWrapHoverRadius}
								onChange={(value) => {
									setAttributes({ mainWrapHoverRadius: value })
								}}
								resRequiredProps={resRequiredProps}
							/>
						</>
					}
				/>
			</PanelBody>

			<PanelBody title={__('Shadow', 'fluentform-block')} initialOpen={false}>
				<TabPanelControl
					normalContent={
						<BoxShadow
							value={mainWrapShadow}
							onChange={val => setAttributes({ mainWrapShadow: val })}
						/>
					}
					hoverContent={
						<BoxShadow
							value={mainWrapHoverShadow}
							onChange={val => setAttributes({ mainWrapHoverShadow: val })}
						/>
					}
				/>
			</PanelBody>

			<PanelBody title={__('Show/Hide Condition', 'fluentform-block')} initialOpen={false}>
				<ToggleControl
					label={__("Hide on desktop", "fluentform-block")}
					className="ffblock-control-field"
					checked={mainWrapShowHide['lg']}
					onChange={(val) =>
						setAttributes({ mainWrapShowHide: { ...mainWrapShowHide, lg: val } })
					}
				/>
				<ToggleControl
					label={__("Hide on Tablet", "fluentform-block")}
					className="ffblock-control-field"
					checked={mainWrapShowHide['md']}
					onChange={(val) =>
						setAttributes({ mainWrapShowHide: { ...mainWrapShowHide, md: val } })
					}
				/>
				<ToggleControl
					label={__("Hide on Mobile", "fluentform-block")}
					className="ffblock-control-field"
					checked={mainWrapShowHide['sm']}
					onChange={(val) =>
						setAttributes({ mainWrapShowHide: { ...mainWrapShowHide, sm: val } })
					}
				/>
			</PanelBody>

			<PanelBody title={__('Z-Index', 'fluentform-block')} initialOpen={false}>
				<Range
					label={__("Z-Index", "fluentform-block")}
					reset={true}
					value={mainWrapZindex}
					onChange={(val) => setAttributes({ mainWrapZindex: val })}
					min={-10000}
					max={999999}
					step={1}
				/>
			</PanelBody>

			<PanelBody title={__('Coustom CSS', 'fluentform-block')} initialOpen={false}>
				<CustomCss
					attributes={attributes}
					setAttributes={setAttributes}
				/>
			</PanelBody>

		</>
	)
}

export default AdvancedControl;