import { __ } from '@wordpress/i18n';
import { Dropdown, Button, SelectControl } from '@wordpress/components';
import RangeDevice from "./RangeDevice";
import FontPicker from "./FontPicker";
import { FONT_WEIGHTS, TEXT_TRANSFORM, FONT_STYLE } from "../globals/constants";
import "./scss/typography.scss";

function Typography(props) {

	const { label, value: data, onChange, resRequiredProps } = props;

	const setSettings = (val, type) => {
		const newData = JSON.parse(JSON.stringify(data));
		newData[type] = val;
		onChange(newData);
	};

	return (
		<div className="ffblock-control-field ffblock-control-typography-wrap">

			{label && (
				<span className="ffblock-label">{label}</span>
			)}

			<div className="ffblock-typography">
				<Dropdown
					className="ffblock-typography-dropdown-icon"
					contentClassName="ffblock-components-popover ffblock-cp-typography-content"
					position="bottom right"
					renderToggle={({ isOpen, onToggle }) => (
						<Button
							isSmall
							onClick={onToggle}
							aria-expanded={isOpen}
							icon="edit"
						></Button>
					)}
					renderContent={() => (

						<div className="ffblock-typography-content">

							<FontPicker
								label={__("Font Family", 'fluentform-block')}
								value={data['family'] || null}
								onChange={(val) => setSettings(val, 'family')}
								className="font-family-base-controll ffblock-control-react-select"
							/>

							<RangeDevice
								label={__('Font Size', 'fluentform-block')}
								responsive={true}
								value={data['size'] || {}}
								units={['px', 'em', '%']}
								min={1}
								max={data["size"]["unit"] === "em" ? 10 : 200}
								step={data["size"]["unit"] === "em" ? 0.1 : 1}
								onChange={(val) => setSettings(val, 'size')}
								resRequiredProps={resRequiredProps}
							/>

							<SelectControl
								label={__("Font Weight", 'fluentform-block')}
								value={data["weight"] || ''}
								options={FONT_WEIGHTS}
								onChange={(val) =>
									setSettings(val, 'weight')
								}
							/>

							<SelectControl
								label={__("Font Style", 'fluentform-block')}
								value={data["style"] || null}
								options={FONT_STYLE}
								onChange={(val) =>
									setSettings(val, 'style')
								}
							/>

							<SelectControl
								label={__("Text Transform", 'fluentform-block')}
								value={data["transform"] || ''}
								options={TEXT_TRANSFORM}
								onChange={(val) =>
									setSettings(val, 'transform')
								}
							/>

							<RangeDevice
								label={__('Letter Spacing', 'fluentform-block')}
								responsive={true}
								value={data['spacing'] || {}}
								units={['px', 'em']}
								min={-5}
								max={data["spacing"]["unit"] === "em" ? 10 : 100}
								step={data["spacing"]["unit"] === "em" ? 0.01 : 1}
								onChange={(val) => setSettings(val, 'spacing')}
								resRequiredProps={resRequiredProps}
							/>

							<RangeDevice
								label={__('Line Height', 'fluentform-block')}
								responsive={true}
								value={data['height'] || {}}
								units={['px', 'em']}
								min={1}
								max={data["height"]["unit"] === "em" ? 10 : 100}
								step={data["height"]["unit"] === "em" ? 0.01 : 1}
								onChange={(val) => setSettings(val, 'height')}
								resRequiredProps={resRequiredProps}
							/>

						</div>
					)}
				/>

			</div>
		</div>
	);
}

export default Typography;
