import CodeMirror from '@uiw/react-codemirror';
import { css } from "@codemirror/lang-css";
import { TextHighlight } from '@wordpress/components';

const CustomCss = ({ attributes, setAttributes }) => {

	const { blockCustomCss } = attributes;
	let defaultCss = `\n {{FFBID}} { \n  /*background-color: red;*/ \n } \n`;

	return (
		<div className='ffblock-codemirror-wrapper'>
			<CodeMirror
				value={blockCustomCss || defaultCss}
				height="200px"
				extensions={[css()]}
				onChange={(value) => setAttributes({ blockCustomCss: value })}
			/>
			<span className='help-text'>
				<TextHighlight
					text="Use {{ FFBID }} prefix before the selector to wrap elements to prevent working globally."
					highlight="{{ FFBID }}" />
			</span>
		</div>
	)
}

export default CustomCss