import { __ } from '@wordpress/i18n';
import { BaseControl, TabPanel } from '@wordpress/components';
import { NORMAL_HOVER } from '../globals/constants';
import './scss/tabPanelControl.scss';

const TabPanelControl = ({ normalContent, hoverContent }) => {

	return (
		<BaseControl>
			<TabPanel
				className="ffblock-nh-tab-panel"
				activeClass="active-tab"
				tabs={NORMAL_HOVER.map(({ label, value }) => ({
					name: value,
					title: label,
					className: `ffblock-tab ${value}`
				}))}
			>
				{(tab) => {
					if (tab?.name === 'normal') {
						return normalContent;
					} else {
						return hoverContent
					}
				}}
			</TabPanel>
		</BaseControl>
	)
}

export default TabPanelControl;