import { addFilter } from '@wordpress/hooks';
import { createHigherOrderComponent } from '@wordpress/compose';
import { useEffect } from '@wordpress/element';

//block sidebar add sticky class
const withInspectorControls = createHigherOrderComponent((BlockEdit) => {
	return (props) => {
		const {
			isSelected,
		} = props;

		useEffect(() => {
			const sidebarEl = document.querySelector('.interface-interface-skeleton__sidebar');
			if (sidebarEl) {
				sidebarEl.classList.add('ffblock-block-sidebar');
				sidebarEl.addEventListener('scroll', function (e) {
					if (e.target.scrollTop > 86) {
						this.classList.add('ffblock-sidebar-collapse');
					} else {
						this.classList.remove('ffblock-sidebar-collapse');
					}
				})
			}
		}, [isSelected])

		return [
			<BlockEdit {...props} />,
		];
	};
}, 'withInspectorControl');

//block sidebar inspector hook
addFilter(
	'editor.BlockEdit',
	'ffblock/fluentform',
	withInspectorControls
);