import Devices from "../Devices";
import { SelectControl } from '@wordpress/components';
import '../scss/SelectBGDevice.scss'

function SelectBGDevice(props) {

	const { label, value, onChange, responsive, name, options, resRequiredProps } = props;
	const { resDevice } = resRequiredProps;

	const setSettings = (val) => {
		const newData = JSON.parse(JSON.stringify(value));
		if (responsive) {
			newData[name][resDevice] = val;
		} else {
			newData[name] = val;
		}
		//newData[device][name] = val;
		onChange(newData);
	}


	return (
		<div className="ffblock-control-field ffblock-control-bg-property">
			<div className="ffblock-control-head">
				<span className="ffblock-label">{label}</span>

				{responsive && (
					<Devices
						resRequiredProps={resRequiredProps}
					/>
				)}

			</div>

			<div className="ffblock-control-body">
				<SelectControl
					value={responsive ? value[name][resDevice] : value[name]}
					options={options}
					onChange={(val) => setSettings(val)}
				/>
			</div>
		</div>

	)
}

export default SelectBGDevice;