import { __ } from '@wordpress/i18n';
import Inspector from './inspector';
import { Spinner } from '@wordpress/components';
import { useBlockProps } from '@wordpress/block-editor';
import { useEffect, useState } from '@wordpress/element';
import apiFetch from '@wordpress/api-fetch';
import classnames from 'classnames';
import styles from './styles';
import { uniqueBlockId, blockEditorWrapperClass, printHtmlStringMarkup } from '../../helpers';
import './editor.scss';

function Edit({ isSelected, attributes, setAttributes, clientId }) {
	const {
		formId,
		layout,
		labelEnable,
		placeholderEnable,
		errorMessageEnable,
		formName
	} = attributes;

	useEffect(() => {
		const BLOCK_PREFIX = 'fluentform-block-';
		uniqueBlockId(BLOCK_PREFIX, clientId, attributes, setAttributes);
	}, []);

	const editorWrapClass = blockEditorWrapperClass(useBlockProps, attributes);

	//generate styles
	const allStyle = styles({
		attributes,
		setAttributes
	})

	// let formLists = JSON.parse(ffbBlockParams.fluent_form_lists);
	// console.log({ formLists });
	// let selectedForm = formLists.filter(obj => {
	// 	return obj.value == formId
	// })

	const blockWrapClass = classnames([
		'ffblock-fluent-form-wrapper',
		'ffblock-fluent-form-style-' + layout,
		!labelEnable && 'ffblock-fm-hide-label',
		!placeholderEnable && 'ffblock-fm-hide-placeholder',
		!errorMessageEnable && 'ffblock-fm-hide-error-message',
		formName == 'inline_subscription' ? 'ffblock-fluentform-subscription' : ''
	]);

	//fetch form html
	const [isHtml, setIsHtml] = useState(false);
	const [formJson, setFormJson] = useState({});

	const apiFetchData = async () => {
		const formData = new window.FormData();
		formData.append('action', 'ffblock_fluentform');
		formData.append('ffblock_nonce_key', ffbBlockParams.ffblock_nonce_key);
		formData.append('formId', formId);
		formData.append('formName', formName);
		try {
			const result = await apiFetch({
				url: ffbBlockParams.ajaxurl,
				method: 'POST',
				body: formData
			});
			if (result?.success) {
				setIsHtml(true);
				setFormJson({ ...result?.data });
			} else {
				setIsHtml(false);
				setFormJson({});
			}
		} catch (error) {
			console.error(error);
		}

	}

	useEffect(() => {
		formId && apiFetchData();
	}, [formId]);

	const hasForm = typeof formJson === 'object' && Object.keys(formJson).length;
	if (!hasForm) {
		return [
			isSelected && (
				<Inspector
					attributes={attributes}
					setAttributes={setAttributes}
				/>
			),
			!isHtml ? (<div className="ffblock-spinner"><Spinner /></div>) : (
				<p>{__('No Fluentform found.', 'fluentform-block')}</p>
			)
		];
	}


	return [
		isSelected && (
			<Inspector
				attributes={attributes}
				setAttributes={setAttributes}
			/>
		),

		<style>{`${allStyle}`}</style>,

		<div {...editorWrapClass}>
			<div className={blockWrapClass}>

				{!ffbBlockParams.is_fluent_form_active && (
					<p>
						{__('Fluent form is not installed on your site please install and activate ', 'fluentform-block')}
						<a href={ffbBlockParams.admin_url + 'plugin-install.php?s=fluent form&tab=search&type=term'} target={'_blank'}>{__('Fluent Form', 'fluentform-block')}</a>
					</p>
				)}

				{(ffbBlockParams.is_fluent_form_active && formId) && (
					<div dangerouslySetInnerHTML={printHtmlStringMarkup(formJson?.html)} />
				)}

				{!formId && (
					<p>{__('Please Select A Form', 'fluentform-block')}</p>
				)}
			</div>
		</div>
	]

}

export default Edit;