const generateDimensionCss = (data, property, important) => {

	let _css = '';
	const unit = data.unit || 'px';

	if (data?.isLinked) {
		if (data.value != '   ') {
			const value = data.value.split(' ')[0];
			_css = `${property}:${value || '0'}${unit}${important}; `
		}
	} else {
		let temp = data.value ? data.value.split(" ") : ['0', '0', '0', '0'];

		if (temp[0] === '' || temp[1] === '' || temp[2] === '' || temp[3] === '') {

			if (property == 'margin' || property == 'padding') {
				if (temp[0]) {
					_css += `${property}-top:${temp[0]}${temp[0] !== '0' ? unit : ''}${important};`
				}
				if (temp[1]) {
					_css += `${property}-right:${temp[1]}${temp[1] !== '0' ? unit : ''}${important};`
				}
				if (temp[2]) {
					_css += `${property}-bottom:${temp[2]}${temp[2] !== '0' ? unit : ''}${important};`
				}
				if (temp[3]) {
					_css += `${property}-left:${temp[3]}${temp[3] !== '0' ? unit : ''}${important};`
				}
			}

			if (property == 'border-radius') {
				if (temp[0]) {
					_css += `border-top-left-radius:${temp[0]}${temp[0] !== '0' ? unit : ''}${important};`
				}
				if (temp[1]) {
					_css += `border-top-right-radius:${temp[1]}${temp[1] !== '0' ? unit : ''}${important};`
				}
				if (temp[2]) {
					_css += `border-bottom-right-radius:${temp[2]}${temp[2] !== '0' ? unit : ''}${important};`
				}
				if (temp[3]) {
					_css += `border-bottom-left-radius:${temp[3]}${temp[3] !== '0' ? unit : ''}${important};`
				}
			}

			if (property == 'border-width') {
				if (temp[0]) {
					_css += `border-top-width:${temp[0]}${temp[0] !== '0' ? unit : ''}${important};`
				}
				if (temp[1]) {
					_css += `border-right-width:${temp[1]}${temp[1] !== '0' ? unit : ''}${important};`
				}
				if (temp[2]) {
					_css += `border-bottom-width:${temp[2]}${temp[2] !== '0' ? unit : ''}${important};`
				}
				if (temp[3]) {
					_css += `border-left-width:${temp[3]}${temp[3] !== '0' ? unit : ''}${important};`
				}
			}

		} else {
			_css = `${property}:${temp[0] ? temp[0] : `0`}${unit} ${temp[1] ? temp[1] : `0`}${unit} ${temp[2] ? temp[2] : `0`}${unit} ${temp[3] ? temp[3] : `0`}${unit}${important};`;
		}
	}

	return _css;
}


function dimensionStyle({ controlValue, property, important = '' }) {

	let deskDimension = '';
	let tabDimension = '';
	let mobDimension = '';

	if (controlValue !== undefined) {
		const deskData = controlValue['lg'] || '';
		const tabData = controlValue['md'] || '';
		const mobData = controlValue['sm'] || '';
		important = important ? ' !important' : '';

		if (deskData && deskData?.value) {
			deskDimension = generateDimensionCss(deskData, property, important);
			if (property == 'border-width' && (controlValue?.borderStyle || controlValue?.borderColor)) {
				deskDimension += `
				border-color:${controlValue.borderColor}${important};
				border-style:${controlValue.borderStyle}${important};
			`;
			}
		}

		if (tabData && tabData?.value) {
			tabDimension = generateDimensionCss(tabData, property, important);
		}

		if (mobData && mobData?.value) {
			mobDimension = generateDimensionCss(mobData, property, important);
		}
	}

	return {
		deskDimension,
		tabDimension,
		mobDimension,
	}
}

export default dimensionStyle;

