function rangeDeviceStyle({ controlValue, property, important = '' }) {
	let rangeDeviceDesk = '';
	let rangeDeviceTab = '';
	let rangeDeviceMob = '';

	if (controlValue !== undefined) {
		const customUnit = controlValue['unit'] || '';
		const deskRange = controlValue['lg'] || '';
		const tabRange = controlValue['md'] || '';
		const mobRange = controlValue['sm'] || '';
		important = important ? ' !important' : '';

		rangeDeviceDesk = deskRange || deskRange === 0 ? property +
			":" + (customUnit !== "px" && deskRange > 100 ? 100 : deskRange) + customUnit + important + ";" : "";

		rangeDeviceTab = tabRange || tabRange === 0 ? property +
			":" + (customUnit !== "px" && tabRange > 100 ? 100 : tabRange) + customUnit + important + ";" : "";

		rangeDeviceMob = mobRange || mobRange === 0 ? property +
			":" + (customUnit !== "px" && mobRange > 100 ? 100 : mobRange) + customUnit + important + ";" : "";
	}

	return {
		rangeDeviceDesk,
		rangeDeviceTab,
		rangeDeviceMob
	}

}
export default rangeDeviceStyle;