import { softMinifyCssStrings, renderBlockCustomCss } from '../helpers/helperFunc';
import {
	boxshadowStyle,
	backgroundStyle,
	dimensionStyle,
	rangeDeviceStyle
} from '../helpers';

function mainWrapperStyle({ attributes, setAttributes }) {
	const {
		blockId,
		mainWrapMarginTop,
		mainWrapMarginBottom,
		mainWrapMargin,
		mainWrapPadding,
		mainWrapBG,
		mainWrapHoverBG,
		mainWrapHoverBGTransition,
		mainWrapBGOverlayEnable,
		mainWrapBGOverlay,
		mainWrapBorder,
		mainWrapHoverBorder,
		mainWrapRadius,
		mainWrapHoverRadius,
		mainWrapShadow,
		mainWrapHoverShadow,
		mainWrapZindex,
		mainWrapShowHide,
		blockCustomCss
	} = attributes;

	const {
		deskDimension: mainWrapMarginStyleDesk,
		tabDimension: mainWrapMarginStyleTab,
		mobDimension: mainWrapMarginStyleMob
	} = dimensionStyle({
		controlValue: mainWrapMargin,
		property: 'margin',
		important: true
	});

	const {
		rangeDeviceDesk: mainWrapMarginTopStyleDesk,
		rangeDeviceTab: mainWrapMarginTopStyleTab,
		rangeDeviceMob: mainWrapMarginTopStyleMob
	} = rangeDeviceStyle({
		controlValue: mainWrapMarginTop,
		property: 'margin-top',
		important: true
	});

	const {
		rangeDeviceDesk: mainWrapMarginBottomStyleDesk,
		rangeDeviceTab: mainWrapMarginBottomStyleTab,
		rangeDeviceMob: mainWrapMarginBottomStyleMob
	} = rangeDeviceStyle({
		controlValue: mainWrapMarginBottom,
		property: 'margin-bottom',
		important: true
	});

	const {
		deskDimension: mainWrapPaddingStyleDesk,
		tabDimension: mainWrapPaddingStyleTab,
		mobDimension: mainWrapPaddingStyleMob
	} = dimensionStyle({
		controlValue: mainWrapPadding,
		property: 'padding',
		important: true
	});

	const {
		deskBackground: mainWrapBgStyleDesk,
		tabBackground: mainWrapBgStyleTab,
		mobBackground: mainWrapBgStyleMob
	} = backgroundStyle({
		controlValue: mainWrapBG
	});

	const {
		deskBackground: mainWrapHoverBgStyleDesk,
		tabBackground: mainWrapHoverBgStyleTab,
		mobBackground: mainWrapHoverBgStyleMob
	} = backgroundStyle({
		controlValue: mainWrapHoverBG
	});

	const {
		deskBackground: mainWrapBGOverlayStyleDesk,
		tabBackground: mainWrapBGOverlayStyleTab,
		mobBackground: mainWrapBGOverlayStyleMob
	} = backgroundStyle({
		controlValue: mainWrapBGOverlay
	});

	const {
		deskDimension: mainWrapBorderStyleDesk,
		tabDimension: mainWrapBorderStyleTab,
		mobDimension: mainWrapBorderStyleMob
	} = dimensionStyle({
		controlValue: mainWrapBorder,
		property: 'border-width',
		important: true
	});

	const {
		deskDimension: mainWrapHoverBorderStyleDesk,
		tabDimension: mainWrapHoverBorderStyleTab,
		mobDimension: mainWrapHoverBorderStyleMob
	} = dimensionStyle({
		controlValue: mainWrapHoverBorder,
		property: 'border-width',
		important: true
	});

	const {
		deskDimension: mainWrapRadiusStyleDesk,
		tabDimension: mainWrapRadiusStyleTab,
		mobDimension: mainWrapRadiusStyleMob
	} = dimensionStyle({
		controlValue: mainWrapRadius,
		property: 'border-radius',
		important: true
	});

	const {
		deskDimension: mainWrapHoverRadiusStyleDesk,
		tabDimension: mainWrapHoverRadiusStyleTab,
		mobDimension: mainWrapHoverRadiusStyleMob
	} = dimensionStyle({
		controlValue: mainWrapHoverRadius,
		property: 'border-radius',
		important: true
	});

	const {
		boxshadowStyles: mainWrapShadowStyle,
	} = boxshadowStyle({
		controlValue: mainWrapShadow,
	});

	const {
		boxshadowStyles: mainWrapHoverShadowStyle,
	} = boxshadowStyle({
		controlValue: mainWrapHoverShadow,
	});

	const wrapperStylesDesk = `
		.${blockId}{
			${mainWrapMarginTopStyleDesk}
			${mainWrapMarginBottomStyleDesk}
			${mainWrapMarginStyleDesk}
			${mainWrapPaddingStyleDesk}
			${mainWrapBgStyleDesk}
			${mainWrapHoverBGTransition ? `transition: background ${mainWrapHoverBGTransition}s;` : ''}
			${mainWrapBorderStyleDesk}
			${mainWrapRadiusStyleDesk}
			${mainWrapShadowStyle}
			${mainWrapZindex ? `z-index:${mainWrapZindex};` : ''}
			transition: all 0.25s;
			position:relative;
		}
		.${blockId}:hover{
			${mainWrapHoverBgStyleDesk}
			${mainWrapHoverBorderStyleDesk}
			${mainWrapHoverRadiusStyleDesk}
			${mainWrapHoverShadowStyle}
		}
		.${blockId}::before{
			content: "";
			position:absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			z-index:-1;
			${mainWrapBGOverlayEnable ? `
			${mainWrapBGOverlayStyleDesk}
			`: ''}
		}
	`

	const wrapperStylesTab = `
		.${blockId}{
			${mainWrapMarginTopStyleTab}
			${mainWrapMarginBottomStyleTab}
			${mainWrapMarginStyleTab}
			${mainWrapPaddingStyleTab}
			${mainWrapBgStyleTab}
			${mainWrapBorderStyleTab}
			${mainWrapRadiusStyleTab}
		}
		.${blockId}:hover{
			${mainWrapHoverBgStyleTab}
			${mainWrapHoverBorderStyleTab}
			${mainWrapHoverRadiusStyleTab}
		 }
		 .${blockId}::before{
			${mainWrapBGOverlayEnable ? `
			${mainWrapBGOverlayStyleTab}			
			`: ''}
		}
	`
	const wrapperStylesMob = `
		.${blockId}{
			${mainWrapMarginTopStyleMob}
			${mainWrapMarginBottomStyleMob}
			${mainWrapMarginStyleMob}
			${mainWrapPaddingStyleMob}
			${mainWrapBgStyleMob}
			${mainWrapBorderStyleMob}
			${mainWrapRadiusStyleMob}
		}
		.${blockId}:hover{
			${mainWrapHoverBgStyleMob}
			${mainWrapHoverBorderStyleMob}
			${mainWrapHoverRadiusStyleMob}
		 }
		 .${blockId}::before{
			${mainWrapBGOverlayEnable ? `			
			${mainWrapBGOverlayStyleMob}
			`: ''}
		}
	`
	const blockWriteCss = softMinifyCssStrings(
		renderBlockCustomCss(blockCustomCss, blockId)
	)

	return {
		wrapperStylesDesk,
		wrapperStylesTab,
		wrapperStylesMob,
		blockWriteCss,
	}
}

export default mainWrapperStyle;