
export const _device = (val, selector) => {
	let data = {};
	if (val && val.lg) {
		data.lg = selector.replace(new RegExp('{{key}}', "g"), val.lg + (val.unit || ''));
	}
	if (val && val.md) {
		data.md = selector.replace(new RegExp('{{key}}', "g"), val.md + (val.unit || ''));
	}
	if (val && val.sm) {
		data.sm = selector.replace(new RegExp('{{key}}', "g"), val.sm + (val.unit || ''));
	}
	return data;
};

export const _push = (val, data) => {
	if (val.lg) {
		data.lg.push(val.lg);
	}
	if (val.md) {
		data.md.push(val.md);
	}
	if (val.sm) {
		data.sm.push(val.sm);
	}
	return data;
};
