import { __ } from '@wordpress/i18n';
import { SelectControl, BaseControl } from '@wordpress/components';
import Color from './Color';
import Dimension from "./Dimension";
import { BORDER_STYLES } from '../globals/constants'

function BorderDimension({ label, value, onChange, resRequiredProps }) {

	const setSettings = (val, type) => {
		onChange(Object.assign({}, value, { [type]: (type == 'lg') ? val.lg : val }));
	}

	return (
		<div className="ffblock-control-field ffblock-control-border-wrap">

			<BaseControl label={label || ''}>
				<SelectControl
					label={__('Border Style', 'fluentform-block')}
					value={value['borderStyle']}
					options={BORDER_STYLES}
					onChange={(val) => setSettings(val, 'borderStyle')}
				/>

				{(value['borderStyle'] !== 'none' && value['borderStyle']) && (
					<>
						<Color
							label={__('Border Color', 'fluentform-block')}
							color={value['borderColor']}
							onChange={(val) => setSettings(val, 'borderColor')}
						/>

						<Dimension
							label="Border Width"
							type="borderWidth"
							responsive={true}
							value={value}
							onChange={(val) => setSettings(val, 'lg')}
							resRequiredProps={resRequiredProps}
						/>

					</>
				)}
			</BaseControl>

		</div>
	)
}

export default BorderDimension;