import { __ } from '@wordpress/i18n';
import { Tooltip } from '@wordpress/components';
import "./scss/alignment.scss";
import Devices from "./Devices";

function Alignment({ value, responsive, onChange, label, content, options, resRequiredProps, help }) {

	const { resDevice } = resRequiredProps;

	const filterSingleVal = () => {
		return value ? (responsive ? (value[resDevice]) : value) : '';
	}

	const setSettings = (val) => {
		if (val == '') {
			return;
		}
		const final = responsive ? Object.assign({}, value, { [resDevice]: val }) : val;
		onChange(final);
	}

	const defaultData = options && Array.isArray(options) ? options : ['left', 'center', 'right', 'justify'];

	return (
		<div className="ffblock-control-field ffblock-control-alignment-wrap">

			{(label || responsive) && (

				<div className="ffblock-control-head">

					{label && (
						<span className="ffblock-label">{label}</span>
					)}

					{responsive && (
						<Devices
							resRequiredProps={resRequiredProps}
						/>
					)}
				</div>
			)}

			<div className="ffblock-control-body ffblock-btn-group">

				{defaultData.map((data, index) => {
					if (content) {
						return (
							<>
								<button className={(filterSingleVal() == data.value ? 'active' : '') + ' ffblock-button'} key={index} onClick={() => setSettings(filterSingleVal() == data.value ? '' : data.value)}>
									<Tooltip text={__(data.label, 'fluentform-block')}><span>{__(data.label, 'fluentform-block')}</span></Tooltip>
								</button>
							</>
						)

					} else {
						return (
							<>
								<button className={(filterSingleVal() == data ? 'active' : '') + ' ffblock-button'} key={index} onClick={() => setSettings(filterSingleVal() == data ? '' : data)}>
									{(data == 'left' || data === 'flex-start') &&
										<Tooltip text={__('Left', 'fluentform-block')}>
											<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512" width="1em" height="1em" fill="currentColor"><path d="M256 96H32C14.33 96 0 81.67 0 64C0 46.33 14.33 32 32 32H256C273.7 32 288 46.33 288 64C288 81.67 273.7 96 256 96zM256 352H32C14.33 352 0 337.7 0 320C0 302.3 14.33 288 32 288H256C273.7 288 288 302.3 288 320C288 337.7 273.7 352 256 352zM0 192C0 174.3 14.33 160 32 160H416C433.7 160 448 174.3 448 192C448 209.7 433.7 224 416 224H32C14.33 224 0 209.7 0 192zM416 480H32C14.33 480 0 465.7 0 448C0 430.3 14.33 416 32 416H416C433.7 416 448 430.3 448 448C448 465.7 433.7 480 416 480z" /></svg>
										</Tooltip>
									}
									{data == 'center' &&
										<Tooltip text={__('Middle', 'fluentform-block')}>
											<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512" width="1em" height="1em" fill="currentColor"><path d="M320 96H128C110.3 96 96 81.67 96 64C96 46.33 110.3 32 128 32H320C337.7 32 352 46.33 352 64C352 81.67 337.7 96 320 96zM416 224H32C14.33 224 0 209.7 0 192C0 174.3 14.33 160 32 160H416C433.7 160 448 174.3 448 192C448 209.7 433.7 224 416 224zM0 448C0 430.3 14.33 416 32 416H416C433.7 416 448 430.3 448 448C448 465.7 433.7 480 416 480H32C14.33 480 0 465.7 0 448zM320 352H128C110.3 352 96 337.7 96 320C96 302.3 110.3 288 128 288H320C337.7 288 352 302.3 352 320C352 337.7 337.7 352 320 352z" /></svg>
										</Tooltip>
									}
									{(data == 'right' || data === 'flex-end') &&
										<Tooltip text={__('Right', 'fluentform-block')}>
											<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512" width="1em" height="1em" fill="currentColor"><path d="M416 96H192C174.3 96 160 81.67 160 64C160 46.33 174.3 32 192 32H416C433.7 32 448 46.33 448 64C448 81.67 433.7 96 416 96zM416 352H192C174.3 352 160 337.7 160 320C160 302.3 174.3 288 192 288H416C433.7 288 448 302.3 448 320C448 337.7 433.7 352 416 352zM0 192C0 174.3 14.33 160 32 160H416C433.7 160 448 174.3 448 192C448 209.7 433.7 224 416 224H32C14.33 224 0 209.7 0 192zM416 480H32C14.33 480 0 465.7 0 448C0 430.3 14.33 416 32 416H416C433.7 416 448 430.3 448 448C448 465.7 433.7 480 416 480z" /></svg>
										</Tooltip>
									}
									{(data == 'justify') &&
										<Tooltip text={__('Right', 'fluentform-block')}>
											<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512" width="1em" height="1em" fill="currentColor"><path d="M416 96H32C14.33 96 0 81.67 0 64C0 46.33 14.33 32 32 32H416C433.7 32 448 46.33 448 64C448 81.67 433.7 96 416 96zM416 352H32C14.33 352 0 337.7 0 320C0 302.3 14.33 288 32 288H416C433.7 288 448 302.3 448 320C448 337.7 433.7 352 416 352zM0 192C0 174.3 14.33 160 32 160H416C433.7 160 448 174.3 448 192C448 209.7 433.7 224 416 224H32C14.33 224 0 209.7 0 192zM416 480H32C14.33 480 0 465.7 0 448C0 430.3 14.33 416 32 416H416C433.7 416 448 430.3 448 448C448 465.7 433.7 480 416 480z" /></svg>
										</Tooltip>
									}
								</button>
							</>
						)
					}
				})}
			</div>
			{help && <p className='components-base-control__help'>{help}</p>}
		</div>
	)
}

export default Alignment;