import { __ } from '@wordpress/i18n';
import { withInstanceId } from '@wordpress/compose';
import { BaseControl } from '@wordpress/components';
import { GOOGLE_FONTS } from "../globals/constants";

import Select from 'react-select';

function FontFamilyPicker({
	label,
	value,
	help,
	instanceId,
	onChange,
	className,
	...props
}) {

	const id = `ffblock-font-family-${instanceId}`;

	const fonts = [
		{ value: "", label: __("Default", 'fluentform-block') },
	];

	//Add Google Fonts
	Object.keys(GOOGLE_FONTS).map(k => {
		fonts.push({ value: k, label: k });
	});

	const onChangeSetting = (data) => {
		let selectedFont = data.value;
		const googleFontsAttr = ":100,100italic,200,200italic,300,300italic,400,400italic,500,500italic,600,600italic,700,700italic,800,800italic,900,900italic";
		const link = document.createElement("link");
		link.rel = "stylesheet";

		if (selectedFont) {
			link.href = "https://fonts.googleapis.com/css?family=" + data.value.replace(/ /g, "+") + googleFontsAttr;
			document.head.appendChild(link);
		}

		onChange(data.value);
	};

	return (
		<BaseControl label={label} id={id} help={help} className={className}>
			<Select
				value={value ? { 'value': value, 'label': value } : {}}
				onChange={(val) => onChangeSetting(val)}
				options={fonts}
				closeMenuOnSelect={true}
				{...props}
			/>
		</BaseControl>
	);
}

export default withInstanceId(FontFamilyPicker);