const { __ } = wp.i18n;
const { ToggleControl } = wp.components;
import Range from "./Range";
import Color from "./Color";
import "./scss/boxshadow.scss";

function BoxShadow(props) {
	const { label, value: data, onChange, transition } = props;

	const setSettings = (val, type, property = null) => {
		const newData = JSON.parse(JSON.stringify(data));
		if (type === 'width') {
			newData[type][property] = val;
		} else {
			newData[type] = val;
		}
		onChange(newData);
	};

	return (
		<div className="ffblock-control-field ffblock-control-boxshadow-wrap">

			{label && (
				<span className="ffblock-label">{label}</span>
			)}

			<div className="ffblock-boxshadow">

				<ToggleControl
					label="Inset"
					checked={data['inset']}
					onChange={(val) => setSettings(val, 'inset')}
				/>

				<Color
					label={__("Shadow Color", 'fluentform-block')}
					color={data['color']}
					onChange={(val) => setSettings(val, 'color')}
				/>
				<Range
					label={__("Horizontal Offset", 'fluentform-block')}
					reset={true}
					value={data['width']["top"]}
					onChange={(val) => setSettings(val, 'width', 'top')}
					min={0}
					max={100}
				/>
				<Range
					label={__("Vertical Offset", 'fluentform-block')}
					reset={true}
					value={data['width']["right"]}
					onChange={(val) => setSettings(val, 'width', 'right')}
					min={0}
					max={100}
				/>
				<Range
					label={__("Shadow Blur", 'fluentform-block')}
					reset={true}
					value={data['width']["bottom"]}
					onChange={(val) => setSettings(val, 'width', 'bottom')}
					min={0}
					max={100}
				/>
				<Range
					label={__("Shadow Spread", 'fluentform-block')}
					reset={true}
					value={data['width']["left"]}
					onChange={(val) => setSettings(val, 'width', 'left')}
					min={0}
					max={100}
				/>

				{transition && (
					<Range
						label={__("Shadow Transition", 'fluentform-block')}
						reset={true}
						value={data['transition']}
						onChange={(val) => setSettings(val, 'transition')}
						min={0}
						max={5}
						step={0.1}
					/>
				)}

			</div>
		</div>
	);
};

export default BoxShadow;
