import { Button, ButtonGroup } from '@wordpress/components';
import { BACKGROUND_TYPE } from '../../globals/constants';
import Gradient from '../Gradient';
import BGImage from './BGImage';
import '../scss/background.scss'

function Background({ label, value, onChange, resRequiredProps }) {

	const defaultData = {
		type: 'classic',
		classic: {
			'color': '',
			'img': { 'imgURL': '', 'imgID': '' },
			'imgProperty': {
				'imgPosition': { 'lg': '' },
				'imgAttachment': { 'lg': '' },
				'imgRepeat': { 'lg': '' },
				'imgSize': { 'lg': '' },
			}
		},
		gradient: null,
	};


	const setSettings = (val, type) => {
		onChange(Object.assign({}, defaultData, (value || {}), { [type]: val }));
	}

	return (
		<div className="ffblock-control-field ffblock-control-background-wrap">
			{label && (
				<div className="ffblock-control-head">
					<span className="ffblock-label">{label}</span>
				</div>
			)}
			<div className='ffblock-control-body'>
				<div className="ffblock-control-body-btn-wrap">
					<ButtonGroup className="ffblock-btn-group">
						{BACKGROUND_TYPE.map((item) => (
							<Button
								isLarge
								variant={value['type'] === item.value ? 'primary' : 'secondary'}
								onClick={() => setSettings(item.value, 'type')}
							>
								{item.label}
							</Button>
						))}
					</ButtonGroup>
					{(Object.keys(value['classic']).length != 0 || value['gradient']) && (
						<Button
							isSmall
							className="ffblock-reset-btn"
							icon="image-rotate"
							onClick={() => onChange(defaultData)}
						></Button>
					)}
				</div>

				<div className="ffblock-control-body-content-wrap">
					{value['type'] === "classic" && (
						<BGImage
							value={value['classic']}
							onChange={val => setSettings(val, 'classic')}
							resRequiredProps={resRequiredProps}
						/>
					)}

					{value['type'] === "gradient" && (
						<Gradient
							value={value['gradient']}
							onChange={(val) => setSettings(val, 'gradient')}
						/>
					)}
				</div>
			</div>

		</div>
	)
}

export default Background;