import { __ } from '@wordpress/i18n';
import { useState } from '@wordpress/element';
import { RangeControl, Button } from '@wordpress/components';
import Devices from "./Devices";
import './scss/rangeDevice.scss'

function RangeDevice(props) {

	const { label, value: data, onChange, responsive, min, max, units, step, defultValue = {}, help, resRequiredProps } = props;

	const { resDevice } = resRequiredProps;

	const [unit, setUnit] = useState('px');

	const dataAttributes = { min, max, step };

	const dftData = defultValue;

	const updateUnit = (_unit) => {
		const newData = JSON.parse(JSON.stringify(data));
		newData.unit = _unit;
		onChange(newData);
		setUnit(_unit);
	}

	const setSettings = (val) => {
		const newData = JSON.parse(JSON.stringify(data));
		newData[resDevice] = val
		newData.unit = newData.unit || unit;
		onChange(newData);
	};

	return (
		<div className="ffblock-control-field ffblock-control-range-wrap">

			<div className="ffblock-control-head">

				<div className="rt-left-part">
					{label && (
						<span className="ffblock-label">{label}</span>
					)}

					{responsive && (
						<Devices
							resRequiredProps={resRequiredProps}
						/>
					)}
				</div>

				{units && (
					<div className="rt-right-part">
						<div className="ffblock-units-choices">
							{(units && Array.isArray(units) ? units : ['px', 'em', '%']).map(_unit => (
								<label
									className={(data?.unit === _unit || (!data?.unit && _unit === unit)) ? 'active' : ''}
									onClick={() => updateUnit(_unit)}>
									{_unit}
								</label>
							))}
						</div>
					</div>
				)}

			</div>

			<div className="ffblock-control-body">
				<RangeControl
					className="ffblock-control-field"
					value={data[resDevice]}
					onChange={(val) => {
						setSettings(val)
					}}
					{...dataAttributes}
					help={help}
				/>

				{(data[resDevice] || data[resDevice] !== 0) && (
					<Button
						isSmall
						className="ffblock-reset-btn"
						icon="image-rotate"
						onClick={() => onChange(dftData)}
					></Button>
				)}
			</div>
		</div>
	);
};

export default RangeDevice;
