
import PropTypes from "prop-types";
import Devices from "./Devices";
import "./scss/dimension.scss";

const Dimension = (props) => {
	const { responsive, onChange, units, value: data, type, resRequiredProps } = props;
	const { resDevice } = resRequiredProps;

	const defaultData = { isLinked: true, unit: 'px', value: '' };
	const currentData = responsive ? (data[resDevice] ? data[resDevice] : defaultData) : data || defaultData;

	const updateData = (newData) => {
		newData.type = type;
		onChange(newData);
	}

	const onChangeDimension = (type, newValue) => {
		let formattedValue = currentData.value ? currentData.value.split(" ") : ['0', '0', '0', '0'];
		if (currentData.isLinked) {
			formattedValue = new Array(5).fill(formattedValue[0] || '0');
		} else {
			if (formattedValue.length < 4) {
				formattedValue = new Array(5).fill(formattedValue[0] || '0');
			}
		}
		const [top, right, bottom, left] = formattedValue;
		const newData = JSON.parse(JSON.stringify(data));

		const updatedValue = currentData.isLinked ? `${newValue} ${newValue} ${newValue} ${newValue}` : `${type === 'top' ? `${newValue}` : `${top}`} ${type === 'right' ? `${newValue}` : `${right}`} ${type === 'bottom' ? `${newValue}` : `${bottom}`} ${type === 'left' ? `${newValue}` : `${left}`}`;
		if (responsive) {
			newData[resDevice] = newData[resDevice] ? newData[resDevice] : defaultData;
			newData[resDevice].value = updatedValue;
		} else {
			newData.value = updatedValue;
		}

		updateData(newData);
	}

	const updateUnit = (_unit) => {
		const newData = JSON.parse(JSON.stringify(data));
		if (responsive) {
			newData[resDevice].unit = _unit;
		} else {
			newData.unit = _unit;
		}
		updateData(newData);
	}

	const toggleIsLinked = () => {
		const newData = JSON.parse(JSON.stringify(data));
		if (responsive) {
			newData[resDevice].isLinked = !newData[resDevice].isLinked;
			if (newData[resDevice].isLinked) {
				newData[resDevice].value = newData[resDevice].value ? newData[resDevice].value.split(" ")[0] : '0 0 0 0';
			}
			else {
				if (newData[resDevice].value.length < 4) {
					const [top, right, bottom, left] = new Array(4).fill(newData[resDevice].value || '0');
					newData[resDevice].value = `${top} ${right} ${bottom} ${left}`;
				}
			}
		} else {
			newData.isLinked = !newData.isLinked;
		}
		updateData(newData);
	}

	const dimensionTypes = ['top', 'right', 'bottom', 'left'];
	let dimensionValues;
	if (currentData.isLinked) {
		const firstValue = currentData?.value ? currentData.value.split(" ")[0] : '';
		dimensionValues = new Array(5).fill(firstValue);
	} else {
		dimensionValues = currentData?.value ? currentData.value.split(" ") : ['0', '0', '0', '0']
	}

	return (
		<div className="ffblock-control-field ffblock-control-dimension">
			<div className="ffblock-control-head">
				<div className="rt-left-part">
					<div className="ffblock-label">{props.label}</div>

					{responsive && (
						<Devices
							resRequiredProps={resRequiredProps}
						/>
					)}

				</div>
				<div className="rt-right-part">
					<div className="ffblock-units-choices">
						{(units && Array.isArray(units) ? units : ['px', 'em', '%']).map(_unit => (
							<label
								className={(currentData?.unit === _unit || (!currentData?.unit && _unit === defaultData.unit)) ? 'active' : ''}
								onClick={() => updateUnit(_unit)}>
								{_unit}</label>
						))}
					</div>
				</div>
			</div>
			<div className="ffblock-control-body">
				<div className="ffblock-control-dimensions">
					{dimensionTypes.map((_item, _i) => {
						return (
							<div className="ffblock-control-dimension">
								<input
									type="number"
									value={dimensionValues[_i]}
									data-setting={_item}
									onChange={(e) => onChangeDimension(_item, e.target.value)}
								/>
								<label className="ffblock-control-dimension-label">{_item}</label>
							</div>
						);
					})}

					<div className='ffblock-control-dimension linking'>
						<button
							className={`ffblock-link-dimensions  ${currentData?.isLinked ? "admin-links linked" : "editor-unlink"}`}
							onClick={toggleIsLinked}
						>
							<span
								className={`rt-dm-link-icon dashicons dashicons-${currentData?.isLinked ? "admin-links linked" : "editor-unlink"}`}>
							</span>
						</button>
					</div>
				</div>
			</div>
		</div>
	);
}

Dimension.propTypes = {
	label: PropTypes.string,
	value: PropTypes.object,
	onChange: PropTypes.func.isRequired,
	type: PropTypes.oneOf(['padding', 'margin', 'borderRadius', 'borderWidth']),
};

export default Dimension;
