export const uniqueBlockId = (blockPrefix, clientId, attributes, setAttributes) => {
	const { blockId } = attributes;
	const _client = clientId.substr(0, 13);
	const uniqueId = blockPrefix + _client;
	if (!blockId) {
		setAttributes({ blockId: uniqueId });
	} else if (blockId && blockId !== uniqueId) {
		setAttributes({ blockId: uniqueId });
	}
}


