import { __ } from '@wordpress/i18n';
import { InspectorControls } from '@wordpress/block-editor';
import { PanelBody, SelectControl, ToggleControl } from '@wordpress/components';
import {
	Alignment,
	RangeDevice,
	BoxShadow,
	BorderDimension,
	Dimension,
	Typography,
	Color,
	AdvancedControl,
	HeaderTab,
	TabPanelControl
} from '../../components';

function Inspector ({ attributes, setAttributes }) {
	const {
		resDevice,
		formId,
		labelEnable,
		placeholderEnable,
		errorMessageEnable,

		//style
		formMaxWidth,
		formAlignment,
		labelTypo,
		labelColor,
		labelSpace,
		inputTABGColor,
		inputWidth,
		inputHeight,
		textareaWidth,
		textareaHeight,
		inputTAPadding,
		inputTAMargin,
		inputTABorder,
		inputTARadius,
		inputTAHoverBorder,
		inputTAHoverRadius,

		checkboxRSize,
		checkboxRItemLabelSpace,
		checkboxRItemSpace,
		optionLabelColor,
		checkboxRBgColor,
		checkboxRCheckedColor,
		checkboxRBorderWidth,
		checkboxRBorderColor,
		checkboxRounded,

		placeholderColor,
		placeholderTypo,
		sectionBGColor,
		sectionMargin,
		sectionPadding,
		sectionHTypo,
		sectionHTextColor,
		sectionHBGColor,
		sectionDTypo,
		sectionDTextColor,
		sectionDBGColor,
		sectionHLineColor,

		customHtmlTypo,
		customHtmlColor,
		customHtmlBGColor,
		buttonTypo,
		buttonTextColor,
		buttonBGColor,
		buttonHoverTextColor,
		buttonHoverBGColor,
		buttonWidth,
		buttonHeight,
		buttonPadding,
		buttonMargin,
		buttonBorder,
		buttonRadius,
		buttonHoverBorder,
		buttonHoverRadius,
		buttonShadow,
		buttonHoverShadow,
		successTypo,
		successColor,
		successBGColor,
		successBorderColor,
		errorTypo,
		errorColor,
		errorBGColor,
		errorBorderColor,
	} = attributes;

	const resRequiredProps = {
		setAttributes,
		resDevice,
		attributes,
	};

	const fluentFormLists = JSON.parse(ffbBlockParams.fluent_form_lists);
	return (
		<InspectorControls key={'controls'}>
			<HeaderTab
				generalTab={
					<>
						<PanelBody title={__('General', 'fluentform-block')} initialOpen={true}>
							<SelectControl
								label={__("Fluent Form", "fluentform-block")}
								value={formId}
								options={fluentFormLists}
								onChange={(val) => {
									setAttributes({ formId: val });
									let selectedForm = fluentFormLists.filter(obj => {
										return obj.value == val;
									})[0];
									setAttributes({ formName: selectedForm.template_name });
								}}
								help={__('Form list from plugin fluent form list', "fluentform-block")}
							/>

							<ToggleControl
								label={__("Display Labels", "fluentform-block")}
								checked={labelEnable}
								onChange={(labelEnable) =>
									setAttributes({ labelEnable })
								}
							/>

							<ToggleControl
								label={__("Display Placeholder", "fluentform-block")}
								checked={placeholderEnable}
								onChange={(placeholderEnable) =>
									setAttributes({ placeholderEnable })
								}
							/>

							<ToggleControl
								label={__("Display Error Message", "fluentform-block")}
								checked={errorMessageEnable}
								onChange={(errorMessageEnable) =>
									setAttributes({ errorMessageEnable })
								}
							/>
							<RangeDevice
								label={__('Form Max Width')}
								responsive={true}
								value={formMaxWidth}
								units={['px', '%']}
								min={1}
								max={formMaxWidth['unit'] == '%' ? 100 : 2000}
								step={1}
								resRequiredProps={resRequiredProps}
								onChange={(val) => setAttributes({ formMaxWidth: val })}
							/>
							<Alignment
								label={__("Alignment", "fluentform-block")}
								value={formAlignment}
								responsive={true}
								options={['left', 'center', 'right']}
								resRequiredProps={resRequiredProps}
								onChange={val => {
									setAttributes({ formAlignment: val })
									setAttributes({ formAlignmentHelper: val[resDevice] })
								}}
								help={__('Alignment field working when form max width field value set', "fluentform-block")}
							/>
						</PanelBody>
					</>
				}
				styleTab={
					<>
						{labelEnable && (
							<PanelBody title={__('Label', 'fluentform-block')} initialOpen={true}>
								<Typography
									label={__('Typography', 'fluentform-block')}
									value={labelTypo}
									onChange={(val) => setAttributes({ labelTypo: val })}
									resRequiredProps={resRequiredProps}
								/>
								<Color
									label={__('Color', 'fluentform-block')}
									color={labelColor}
									onChange={(newColor) => setAttributes({
										labelColor: newColor
									})}
								/>
								<Dimension
									label={__("Space", "fluentform-block")}
									onChange={(value) => {
										setAttributes({ labelSpace: value })
									}}
									type="margin"
									responsive={true}
									value={labelSpace}
									resRequiredProps={resRequiredProps}
								/>
							</PanelBody>
						)}

						<PanelBody title={__('Input/Textarea/Select', 'fluentform-block')} initialOpen={false}>
							{/*<Typography*/}
							{/*	label={__('Typography', 'fluentform-block')}*/}
							{/*	value={inputTATypo}*/}
							{/*	onChange={(val) => setAttributes({ inputTATypo: val })}*/}
							{/*	resRequiredProps={resRequiredProps}*/}
							{/*/>*/}
							<RangeDevice
								label={__('Input Width', "fluentform-block")}
								responsive={true}
								value={inputWidth}
								units={['px', '%']}
								min={1}
								max={inputWidth['unit'] === '%' ? 100 : 1000}
								step={1}
								onChange={(val) => setAttributes({ inputWidth: val })}
								resRequiredProps={resRequiredProps}
							/>

							<RangeDevice
								label={__('Input Height', "fluentform-block")}
								responsive={true}
								value={inputHeight}
								units={['px', '%']}
								min={1}
								max={inputHeight['unit'] === '%' ? 100 : 200}
								step={1}
								onChange={(val) => setAttributes({ inputHeight: val })}
								resRequiredProps={resRequiredProps}
							/>

							<Dimension
								onChange={(value) => {
									setAttributes({ inputTAPadding: value })
								}}
								label={__('Padding', "fluentform-block")}
								type="padding"
								responsive={true}
								value={inputTAPadding}
								resRequiredProps={resRequiredProps}
							/>
							<Dimension
								onChange={(value) => {
									setAttributes({ inputTAMargin: value })
								}}
								label={__("Margin", "fluentform-block")}
								type="margin"
								responsive={true}
								value={inputTAMargin}
								resRequiredProps={resRequiredProps}
							/>
							<PanelBody title={__('Border', 'fluentform-block')} initialOpen={false}>

								<TabPanelControl
									normalContent={
										<>
											<BorderDimension
												value={inputTABorder}
												onChange={(inputTABorder) => {
													setAttributes({ inputTABorder })
												}}
												resRequiredProps={resRequiredProps}
											/>

											<Dimension
												onChange={(value) => {
													setAttributes({ inputTARadius: value })
												}}
												label={__("Border Radius", "fluentform-block")}
												type="borderRadius"
												responsive={true}
												value={inputTARadius}
												resRequiredProps={resRequiredProps}
											/>

										</>
									}

									hoverContent={
										<>
											<BorderDimension
												value={inputTAHoverBorder}
												onChange={(inputTAHoverBorder) => {
													setAttributes({ inputTAHoverBorder })
												}}
												resRequiredProps={resRequiredProps}
											/>

											<Dimension
												onChange={(value) => {
													setAttributes({ inputTAHoverRadius: value })
												}}
												label={__('Border Radius', 'fluentform-block')}
												type="borderRadius"
												responsive={true}
												value={inputTAHoverRadius}
												resRequiredProps={resRequiredProps}
											/>
										</>
									}
								/>
							</PanelBody>
							<Color
								label={__('Background Color', "fluentform-block")}
								color={inputTABGColor}
								onChange={(inputTABGColor) => setAttributes({ inputTABGColor })}
							/>
							<hr />
							<h3 className="ffblock-section-label-text">{__('Textarea', 'fluentform-block')}</h3>

							<RangeDevice
								label={__('Textarea Width', "fluentform-block")}
								responsive={true}
								value={textareaWidth}
								units={['px', '%']}
								min={1}
								max={textareaWidth['unit'] === '%' ? 100 : 1000}
								step={1}
								onChange={(val) => setAttributes({ textareaWidth: val })}
								resRequiredProps={resRequiredProps}
							/>

							<RangeDevice
								label={__('Textarea Height', "fluentform-block")}
								responsive={true}
								value={textareaHeight}
								units={['px', '%']}
								min={1}
								max={textareaHeight['unit'] === '%' ? 100 : 400}
								step={1}
								onChange={(val) => setAttributes({ textareaHeight: val })}
								resRequiredProps={resRequiredProps}
							/>
						</PanelBody>

						{placeholderEnable && (
							<PanelBody title={__('Placeholder', 'fluentform-block')} initialOpen={false}>
								<Typography
									label={__('Typography', 'fluentform-block')}
									value={placeholderTypo}
									onChange={(val) => setAttributes({ placeholderTypo: val })}
									resRequiredProps={resRequiredProps}
								/>
								<Color
									label={__('Color', 'fluentform-block')}
									color={placeholderColor}
									onChange={(placeholderColor) => setAttributes({ placeholderColor })}
								/>
							</PanelBody>
						)}

						<PanelBody title={__('Checkbox & Radio', 'fluentform-block')} initialOpen={false}>
							<Color
								label={__('Label Color', "fluentform-block")}
								color={optionLabelColor}
								onChange={(optionLabelColor) => setAttributes({ optionLabelColor })}
							/>

							<Color
								label={__('Background Color', "fluentform-block")}
								color={checkboxRBgColor}
								onChange={(checkboxRBgColor) => setAttributes({ checkboxRBgColor })}
							/>

							<Color
								label={__('Checked Color', "fluentform-block")}
								color={checkboxRCheckedColor}
								onChange={(checkboxRCheckedColor) => setAttributes({ checkboxRCheckedColor })}
							/>

							<RangeDevice
								label={__('Size', "fluentform-block")}
								responsive={true}
								value={checkboxRSize}
								units={['px', '%']}
								min={1}
								max={100}
								step={1}
								onChange={(val) => setAttributes({ checkboxRSize: val })}
								resRequiredProps={resRequiredProps}
							/>

							<RangeDevice
								label={__('Item To Label Space', "fluentform-block")}
								responsive={true}
								value={checkboxRItemLabelSpace}
								units={['px', '%']}
								min={1}
								max={200}
								step={1}
								onChange={(val) => setAttributes({ checkboxRItemLabelSpace: val })}
								resRequiredProps={resRequiredProps}
							/>


							<RangeDevice
								label={__('Item Bottom Space', "fluentform-block")}
								responsive={true}
								value={checkboxRItemSpace}
								units={['px', '%']}
								min={1}
								max={200}
								step={1}
								onChange={(val) => setAttributes({ checkboxRItemSpace: val })}
								resRequiredProps={resRequiredProps}
							/>

							<RangeDevice
								label={__('Border Width', "fluentform-block")}
								responsive={true}
								value={checkboxRBorderWidth}
								units={['px', '%']}
								min={1}
								max={20}
								step={1}
								onChange={(val) => setAttributes({ checkboxRBorderWidth: val })}
								resRequiredProps={resRequiredProps}
							/>

							<Color
								label={__('Border Color', "fluentform-block")}
								color={checkboxRBorderColor}
								onChange={(checkboxRBorderColor) => setAttributes({ checkboxRBorderColor })}
							/>

							<RangeDevice
								label={__('Checkbox Rounded Corners', "fluentform-block")}
								responsive={true}
								value={checkboxRounded}
								units={['px', '%']}
								min={1}
								max={20}
								step={1}
								onChange={(val) => setAttributes({ checkboxRounded: val })}
								resRequiredProps={resRequiredProps}
							/>
						</PanelBody>

						<PanelBody title={__('Section Break', 'fluentform-block')} initialOpen={false}>
							<Color
								label={__('Background Color', 'fluentform-block')}
								color={sectionBGColor}
								onChange={(sectionBGColor) => setAttributes({
									sectionBGColor
								})
								}
							/>
							<Dimension
								onChange={(value) => {
									setAttributes({ sectionMargin: value })
								}}
								label={__("Margin", "fluentform-block")}
								type="margin"
								responsive={true}
								value={sectionMargin}
								resRequiredProps={resRequiredProps}
							/>

							<Dimension onChange={(value) => {
								setAttributes({ sectionPadding: value })
							}}
								label={__("Padding", "fluentform-block")}
								type="padding"
								responsive={true}
								value={sectionPadding}
								resRequiredProps={resRequiredProps}
							/>

							<hr />
							<h3 className="ffblock-section-label-text">{__('Heading', 'fluentform-block')}</h3>

							<Typography
								label={__('Typography', 'fluentform-block')}
								value={sectionHTypo}
								onChange={(val) => setAttributes({ sectionHTypo: val })}
								resRequiredProps={resRequiredProps}
							/>
							<Color
								label={__("Text Color", "fluentform-block")}
								color={sectionHTextColor}
								onChange={(sectionHTextColor) => setAttributes({
									sectionHTextColor
								})
								}
							/>
							<Color
								label={__("Background Color", "fluentform-block")}
								color={sectionHBGColor}
								onChange={(sectionHBGColor) => setAttributes({
									sectionHBGColor
								})
								}
							/>

							<hr />
							<h3 className="ffblock-section-label-text">{__('Description', 'fluentform-block')}</h3>

							<Typography
								label={__('Typography', 'fluentform-block')}
								value={sectionDTypo}
								onChange={(val) => setAttributes({ sectionDTypo: val })}
								resRequiredProps={resRequiredProps}
							/>
							<Color
								label={__("Text Color", "fluentform-block")}
								color={sectionDTextColor}
								onChange={(sectionDTextColor) => setAttributes({
									sectionDTextColor
								})
								}
							/>
							<Color
								label={__("Background Color", "fluentform-block")}
								color={sectionDBGColor}
								onChange={(sectionDBGColor) => setAttributes({
									sectionDBGColor
								})
								}
							/>

							<Color
								label={__("Border Color", "fluentform-block")}
								color={sectionHLineColor}
								onChange={(sectionHLineColor) => setAttributes({
									sectionHLineColor
								})
								}
							/>
						</PanelBody>

						<PanelBody title={__('Custom HTML', 'fluentform-block')} initialOpen={false}>
							<Typography
								label={__('Typography', 'fluentform-block')}
								value={customHtmlTypo}
								onChange={(val) => setAttributes({ customHtmlTypo: val })}
								resRequiredProps={resRequiredProps}
							/>
							<Color
								label={__("Text Color", "fluentform-block")}
								color={customHtmlColor}
								onChange={(customHtmlColor) => setAttributes({
									customHtmlColor
								})
								}
							/>
							<Color
								label={__("Background Color", "fluentform-block")}
								color={customHtmlBGColor}
								onChange={(customHtmlBGColor) => setAttributes({
									customHtmlBGColor
								})
								}
							/>
						</PanelBody>

						<PanelBody title={__('Submit Button', 'fluentform-block')} initialOpen={false}>
							<Typography
								label={__('Typography', 'fluentform-block')}
								value={buttonTypo}
								onChange={(val) => setAttributes({ buttonTypo: val })}
								resRequiredProps={resRequiredProps}
							/>

							<TabPanelControl
								normalContent={<>
									<Color
										label={__("Text Color", "fluentform-block")}
										color={buttonTextColor}
										onChange={(buttonTextColor) => setAttributes({
											buttonTextColor
										})
										}
									/>
									<Color
										label={__("Background Color", "fluentform-block")}
										color={buttonBGColor}
										onChange={(buttonBGColor) => setAttributes({
											buttonBGColor
										})
										}
									/>
								</>}
								hoverContent={<>
									<Color
										label={__("Hover Text Color", "fluentform-block")}
										color={buttonHoverTextColor}
										onChange={(buttonHoverTextColor) => setAttributes({
											buttonHoverTextColor
										})
										}
									/>
									<Color
										label={__("Hover Background Color", "fluentform-block")}
										color={buttonHoverBGColor}
										onChange={(buttonHoverBGColor) => setAttributes({
											buttonHoverBGColor
										})
										}
									/>
								</>}
							/>

							<RangeDevice
								label={__('Width', "fluentform-block")}
								responsive={true}
								value={buttonWidth}
								units={['px', '%']}
								min={1}
								max={buttonWidth['unit'] == '%' ? 100 : 1000}
								step={1}
								onChange={(val) => setAttributes({ buttonWidth: val })}
								resRequiredProps={resRequiredProps}
							/>

							<RangeDevice
								label={__('Height', "fluentform-block")}
								responsive={true}
								value={buttonHeight}
								units={['px', '%']}
								min={1}
								max={buttonHeight['unit'] == '%' ? 100 : 200}
								step={1}
								onChange={(val) => setAttributes({ buttonHeight: val })}
								resRequiredProps={resRequiredProps}
							/>
							<Dimension
								onChange={(value) => {
									setAttributes({ buttonPadding: value })
								}}
								label={__("Padding", "fluentform-block")}
								type="padding"
								responsive={true}
								value={buttonPadding}
								resRequiredProps={resRequiredProps}
							/>

							<Dimension onChange={(value) => {
								setAttributes({ buttonMargin: value })
							}}
								label={__("Margin", "fluentform-block")}
								type="margin"
								responsive={true}
								value={buttonMargin}
								resRequiredProps={resRequiredProps}
							/>


							<PanelBody title={__('Border', 'fluentform-block')} initialOpen={false}>
								<TabPanelControl
									normalContent={
										<>
											<BorderDimension
												value={buttonBorder}
												onChange={(buttonBorder) => {
													setAttributes({ buttonBorder })
												}}
												resRequiredProps={resRequiredProps}
											/>
											<Dimension onChange={(value) => {
												setAttributes({ buttonRadius: value })
											}} label={__('Border Radius', 'fluentform-block')} type="borderRadius" responsive={true} value={buttonRadius}
												resRequiredProps={resRequiredProps}
											/>

										</>
									}
									hoverContent={
										<>
											<BorderDimension
												value={buttonHoverBorder}
												onChange={(buttonHoverBorder) => {
													setAttributes({ buttonHoverBorder })
												}}
												resRequiredProps={resRequiredProps}
											/>
											<Dimension onChange={(value) => {
												setAttributes({ buttonHoverRadius: value })
											}} label={__('Border Radius', 'fluentform-block')} type="borderRadius" responsive value={buttonHoverRadius}
												resRequiredProps={resRequiredProps}
											/>
										</>
									}
								/>
							</PanelBody>

							<PanelBody title={__('Box Shadow', 'fluentform-block')} initialOpen={false}>
								<TabPanelControl
									normalContent={
										<BoxShadow value={buttonShadow} onChange={val => setAttributes({ buttonShadow: val })} />
									}
									hoverContent={
										<BoxShadow value={buttonHoverShadow} onChange={val => setAttributes({ buttonHoverShadow: val })} />
									}
								/>
							</PanelBody>
						</PanelBody>

						<PanelBody title={__('Success Message', 'fluentform-block')} initialOpen={false}>

							<Typography
								label={__('Typography', 'fluentform-block')}
								value={successTypo}
								onChange={(val) => setAttributes({ successTypo: val })}
								resRequiredProps={resRequiredProps}
							/>

							<Color
								label={__('Color', 'fluentform-block')}
								color={successColor}
								onChange={(successColor) => setAttributes({
									successColor
								})
								}
							/>

							<Color
								label={__('Background Color', 'fluentform-block')}
								color={successBGColor}
								onChange={(successBGColor) => setAttributes({
									successBGColor
								})
								}
							/>

							<Color
								label={__('Border Color', 'fluentform-block')}
								color={successBorderColor}
								onChange={(successBorderColor) => setAttributes({
									successBorderColor
								})
								}
							/>
						</PanelBody>

						<PanelBody title={__('Error Message', 'fluentform-block')} initialOpen={false}>

							<Typography
								label={__('Typography', 'fluentform-block')}
								value={errorTypo}
								onChange={(val) => setAttributes({ errorTypo: val })}
								resRequiredProps={resRequiredProps}
							/>

							<Color
								label={__('Color', 'fluentform-block')}
								color={errorColor}
								onChange={(errorColor) => setAttributes({
									errorColor
								})
								}
							/>

							<Color
								label={__('Background Color', 'fluentform-block')}
								color={errorBGColor}
								onChange={(errorBGColor) => setAttributes({
									errorBGColor
								})
								}
							/>
							<Color
								label={__('Border Color', 'fluentform-block')}
								color={errorBorderColor}
								onChange={(errorBorderColor) => setAttributes({
									errorBorderColor
								})
								}
							/>
						</PanelBody>
					</>
				}
				advancedTab={
					<>
						<AdvancedControl
							attributes={attributes}
							setAttributes={setAttributes}
						/>
					</>
				}
			/>
		</InspectorControls>
	)

}

export default Inspector;