import { useCallback, useRef, useState } from "@wordpress/element";
import { __ } from '@wordpress/i18n';
import './scss/devices.scss'
import useClickOutside from "../helpers/useClickOutside";
import { dispatch } from "@wordpress/data";

const Devices = ({ resRequiredProps }) => {
	const { resDevice, setAttributes } = resRequiredProps;
	const [switcherIsOpen, setSwitcherIsOpen] = useState(false);
	const devicesRef = useRef();
	const closeDevices = useCallback(() => setSwitcherIsOpen(false), []);

	const onClickHandler = (device) => {
		setAttributes({ resDevice: device });
		let deviceType = device == 'lg' ? 'Desktop' :
			device == 'md' ? 'Tablet' :
				device == 'sm' ? 'Mobile' : 'Desktop';
		dispatch('core/edit-post').__experimentalSetPreviewDeviceType(deviceType);
		setSwitcherIsOpen(() => !switcherIsOpen)
	}
	useClickOutside(devicesRef, closeDevices);

	return <div ref={devicesRef} className={`ffblock-device-switchers active-${resDevice}${switcherIsOpen ? ' ffblock-device-switchers-open' : ''} `}
		onClick={() => setSwitcherIsOpen(() => !switcherIsOpen)}>

		<div className="ffblock-device-switchers-wrap">
			<a className={`ffblock-device-switcher ffblock-device-switcher-desktop${resDevice === 'lg' ? ' active' : ''}`}
				onClick={() => onClickHandler('lg')} data-tooltip={__('Desktop', 'fluentform-block')}>
				<i className="dashicons dashicons-desktop" />
			</a>

			<a className={`ffblock-device-switcher ffblock-device-switcher-laptop${resDevice === 'md' ? ' active' : ''}`}
				onClick={() => onClickHandler('md')} data-tooltip={__('Tablet', 'fluentform-block')}>
				<i className="dashicons dashicons-tablet" />
			</a>

			<a className={`ffblock-device-switcher ffblock-device-switcher-tablet${resDevice === 'sm' ? ' active' : ''}`}
				onClick={() => onClickHandler('sm')} data-tooltip={__('Mobile', 'fluentform-block')}>
				<i className="dashicons dashicons-smartphone" />
			</a>

		</div>
	</div>
}

export default Devices;